          Component.options.__sourceCode = "<template>\n<article>\n    <section>\n        <p class=\"controller\">修改窗口高度 <Input v-model=\"v\" /></p>\n        <p class=\"controller\">修改子项高度 <Input v-model=\"v1\" /></p>\n    </section>\n    <section>\n        <div\n            class=\"box\"\n            :style=\"{\n                border: '1px dashed',\n                'margin-top': 5,\n                height: v + 'px'\n            }\"\n        >\n            <LongList style=\"height: 100%\" :data=\"list\" :threshold=\"2\">\n                <div\n                    :style=\"{ height: v1 + 'px' }\"\n                    slot-scope=\"{ props }\"\n                    :index=\"props.item.id\"\n                    class=\"item\"\n                >\n                    <img :src=\"props.item.img\" alt=\"\" />\n                    <div>\n                        下标：{{ props.index }}内容：{{ props.item.text }}\n                    </div>\n                </div>\n            </LongList>\n        </div>\n    </section>\n</article>\n</template>\n\n<style lang=\"scss\" scoped>\n.controller {\n    margin-bottom: 20px;\n}\n.item {\n    overflow: hidden;\n    padding: 15px;\n    & > * {\n        float: left;\n    }\n    & > img {\n        width: 35px;\n        height: 35px;\n    }\n    border-bottom: 1px solid #ccc;\n    &::after {\n        content: '';\n        display: block;\n        clear: both;\n    }\n}\n</style>\n\n<script>\nexport default {\n    data() {\n        return {\n            v: 360,\n            v1: 66,\n            list: (() => {\n                let d = []\n                for (var i = 0; i < 10000; i++) {\n                    d.push({\n                        id: i,\n                        text: 'item #' + (i + 1),\n                        img:\n                            'https://ss2.baidu.com/-vo3dSag_xI4khGko9WTAnF6hhy/image/h%3D300/sign=dc7e87bdfb03738dc14a0a22831ab073/08f790529822720e698c1ae776cb0a46f21fab69.jpg'\n                    })\n                }\n  