export default function (Component) {
          Component.options.__sourceCode = "<template>\n<Select\n    :data=\"listGroup\"\n    placeholder=\"分组\"\n    multiple\n    filterable\n    v-model=\"value\"\n    value-name=\"id\"\n    label-name=\"text\"\n    child-name=\"children\"\n>\n    <div slot-scope=\"{ props }\">\n        {{ props.item.text }}\n    </div>\n</Select>\n</template>\n\n<script>\nexport default {\n    data() {\n        return {\n            listGroup: (() => {\n                let d = []\n                for (let i = 0; i < 4; i++) {\n                    let x = {\n                        text: '这是第' + i + '组',\n                        children: []\n                    }\n                    for (let j = 0; j < 5; j++) {\n                        x.children.push({\n                            id: i + '/' + j,\n                            text: 'item#' + (i + '/' + (j + 1))\n                        })\n                    }\n                    d.push(x)\n                }\n                return d\n            })(),\n            value: ''\n        }\n    }\n}\n</script>\n";
