          Component.options.__sourceCode = "<template>\n<article>\n    <section class=\"section\">\n        <Menu\n            :mode=\"mode\"\n            :high-color=\"isHighColor\"\n            :accordion=\"accordion\"\n            :thumbnail=\"thumbnail\"\n        >\n            <MenuOption name=\"1\">\n                <Icon iconname=\"home\" thumbnail-icon />\n                首页\n            </MenuOption>\n            <MenuSub\n                v-for=\"(item, index) in menu\"\n                :key=\"index + ''\"\n                :name=\"index + 2\"\n            >\n                <template #title>\n                    <Icon iconname=\"folder\" thumbnail-icon />\n                    {{ item.title }}\n                </template>\n                <MenuGroup\n                    v-for=\"(innerItem, innerIndex) in item.group\"\n                    :key=\"innerIndex + ''\"\n                >\n                    <template #title>{{ innerItem.title }}</template>\n                    <MenuOption\n                        v-for=\"(menuItem, menuIndex) in innerItem.list\"\n                        :key=\"menuIndex + ''\"\n                        :name=\"\n                            index +\n                                2 +\n                                '-' +\n                                (innerIndex + 1) +\n                                '-' +\n                                (menuIndex + 1)\n                        \"\n                    >\n                        {{ menuItem }}\n                    </MenuOption>\n                </MenuGroup>\n            </MenuSub>\n            <MenuSub name=\"4\">\n                <MenuOption name=\"4-1\" />\n                <MenuOption name=\"4-2\" />\n                <MenuSub name=\"4-3\">\n                    <MenuOption name=\"4-3-1\" disabled />\n                    <MenuOption name=\"4-3-2\" />\n                    <MenuSub name=\"4-3-3\">\n                        <MenuOption name=\"4-3-3-1\" />\n                        