          Component.options.__sourceCode = "<template>\n<div class=\"demo\">\n    <Layout class=\"container-demo\">\n        <Header>\n            <Menu high-color mode=\"horizontal\">\n                <MenuOption name=\"1\" />\n                <MenuOption name=\"2\" />\n                <MenuSub name=\"3\">\n                    <MenuGroup>\n                        <MenuOption name=\"3-1-1\" />\n                        <MenuOption name=\"3-1-2\" />\n                    </MenuGroup>\n                    <MenuGroup>\n                        <MenuOption name=\"3-2-1\" />\n                        <MenuOption name=\"3-2-2\" />\n                    </MenuGroup>\n                </MenuSub>\n            </Menu>\n        </Header>\n        <Layout>\n            <Sider\n                v-model=\"collapsed\"\n                breakpoint=\"sm\"\n                @on-collapse=\"collapse\"\n            >\n                <Menu :thumbnail=\"collapsed\">\n                    <MenuOption name=\"1\" />\n                    <MenuOption name=\"2\" />\n                    <MenuSub name=\"3\">\n                        <MenuGroup>\n                            <MenuOption name=\"3-1-1\" />\n                            <MenuOption name=\"3-1-2\" />\n                        </MenuGroup>\n                        <MenuGroup>\n                            <MenuOption name=\"3-2-1\" />\n                            <MenuOption name=\"3-2-2\" />\n                        </MenuGroup>\n                    </MenuSub>\n                </Menu>\n            </Sider>\n            <Content>\n                <div class=\"sider-bar\" @click=\"collapsed = !collapsed\">\n                    <Icon\n                        :iconname=\"\n                            collapsed ? 'arrow-forward' : 'arrow-back'\n                        \"\n                    />\n                </div>\n                Content\n            </Content>\n        </Layout>\n        <Footer>Footer</Footer>\n    </Layout>\n</div>\n</template>